@import url('https://fonts.googleapis.com/css2?family=Arsenal+SC:ital,wght@0,400;0,700;1,400;1,700&display=swap');

.grocery-page{
    background-color: white;
    margin:10px;
    margin-bottom: 20px;
    border-radius: 5px;
    padding: 10px;
    font-family: Static;
}
.page-items{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
    margin: 10px;
    gap: 25px;
    row-gap: 40px;
}