
@import url('https://fonts.googleapis.com/css2?family=Teko:wght@300..700&display=swap');
.category{
    display: flex;
    justify-content: space-between;
    background-color: white;
    margin-top: 10px;
    padding: 10px;
    margin-left: 15px;
    margin-right: 15px;
    border-radius: 5px;
    overflow-y: scroll;
}
.category::-webkit-scrollbar{
    display: none;
}
.category .cat-Item{
    text-align: center;
    padding: 5px;
    cursor: pointer;
}

.category .cat-Item p{
    font-size: 25px;
    color: black;
    font-weight: 400;
    padding-top: 3px;
    font-family: Teko;
}
.category .cat-Item img{
    width: 120px;
    height: 80px;
}
@media (max-width:768px) {
    .category{
        margin-top: 10px;
        padding: 5px;
        margin-left: 10px;
        margin-right: 10px;
        border-radius: 5px;
    }
    .category .cat-Item{
        padding: 2px;
    }
    .category .cat-Item img {
        width: 100px;
        height: 70px;
    }
    .category .cat-Item p{
        font-size: 15px;
    }
}