.signContainer{
        position: absolute;
        z-index: 1;
        width: 100%;
        height: 100%;
        background-color: #00000090;
        display: grid;
}
.sign-pop{
    place-self: center;
    width: max(23vw, 330px);
    color: gray;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    gap:20px;
    padding: 25px 30px;
    border-radius: 8px;
    font-size: 14px;
    animation: fadeIn .5s;
}
.sign-head{
    font-size: 20px;
    color: black;
    font-weight: bold;
}
.sign-input-container input{
    width: 95%;
    border-radius: 5px;
    margin-bottom: 12px;
    padding: 10px;
    border: 1px solid #002984;
    font-size: 15px;
    outline: none;
}
.sign-login:hover{
    color: blue;
}

