@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
.contactUs-container{
    background-color: #e3f2fd;
    min-height: 40vh;
    display: flex;
    flex-direction: row;
}
.left-box{
    width: 60%;
}
.left-contact-container{
    height: 60vh;
    width: 80%;
    padding-top: 100px;
    padding-left:100px;
}
.first .head{
    color: #212121;
    font-size: 60px;
    font-weight: bold;
    font-family: "Roboto", sans-serif;
}
.first .content{
    padding-top: 15px;
    font-size: 16px;
    width: 50%;
    color: grey;
    font-weight: 500;
     font-family: "Roboto";

}
.first .email,.first .phone{
    padding-top: 17px;
  

}
.first .email a,.first .phone a{
    text-decoration: none;
    color: grey;
    font-weight: 500;
    font-family: "Roboto";
    font-size: 18px;

}

.second{
    padding-top: 60px;
    display: flex;
     justify-content: space-between;
    gap: 30px;
}
.second .head{
    font-size: 23px;
    color: #212121;
    font-weight: 600;
    font-family: "Roboto", sans-serif;
}
.second  .content{
 padding-top: 10px;
 color: grey;
 font-weight: 600;
 font-family: "Roboto";
 font-size: 14px;
}
.contact-box{
width: 40%;
display: flex;
    justify-content: start;
    align-items: center;
}
.contact-container{
    padding: 20px;
    border-radius: 20px;
    box-shadow: 6px 6px 7px 6px rgba(0, 0, 0, 0.123);
background-color: white;
width: 70%;
margin-right: 70px;
height: 60vh;
}
.contact-container .head{
    color: #212121;
    font-size: 35px;
    font-weight: bold;
    font-family: "Roboto", sans-serif;
}
.contact-container .sub-head{
    font-size: 17px;
    color: rgba(0, 0, 0, 0.66);
    padding-top: 2px;
    font-weight: 500;
}
.inputs{
    padding-top: 14px;
}
.inputs input{
    font-size: 15px;
    width: 96%;
    padding: 12px;
    border: 1px solid grey;
    outline: none;
    margin-bottom: 8px;
    border-radius: 22px;
}
.inputs .name {
    display: flex;
    justify-content: space-between;
}
.inputs .name input{
    width: 42%;
}
.inputs textarea{
    width:96%;
    height: 90px;
    padding: 12px;
    border: 1px solid grey;
    border-radius: 18px;
    outline: none;
    font-size: 18px;
}
.inputs .btn{
margin-top: 10px;
}
.inputs .contact-btn button{
    width: 102%;
    border-radius: 22px;
    padding: 10px;
    border:none;
    color: white;
    background-color: blue;
    cursor: pointer;
    font-size: 20px;
}
.inputs .contact-btn button:hover{
    background-color: rgba(0, 0, 255, 0.689);
}
.inputs .term{
    padding-top: 5px;
     width: 100%;
    text-align: center;
}
.inputs .term p{
    font-size: 16px;
    color: rgba(0, 0, 0, 0.612);
    text-align: center;
    width:98%;
}

.bottom-container{
    padding-top: 40px;
    padding-left: 40px;
    background-color: white;
    margin-bottom: -22px;
    padding-bottom: 50px;
    display: flex;
    flex-direction: row;
}
.bottom-container .map-container {
    border-radius: 20px;
}
.bottom-container .info{
    padding-top: 120px;
    padding-left: 20px;
}
.bottom-container .info .sub-head{
    font-size: 22px;
    color: rgba(0, 0, 0, 0.779);
}
.bottom-container .info .main-head{
    padding-top: 3px;
    font-size: 40px;
    color: rgba(0, 0, 0, 0.947);
    font-weight: bold;
}
.head-quarters{
    padding-top: 20px;
}
.head-quarters p{
    color: rgba(0, 0, 0, 0.664);
}
.head-quarters .head-quart{
    font-size: 20px;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.963);
}
.head-quarters  .firs{
    padding-top: 10px;
}
.map-container{
    width: 720px;
    height: 420px;
}
.map-container .iframe{
    width: 100%;
    height: 100%;
}
@media (max-width:1024px) {
    .contact-container{
        max-height: 50vh;
    }
}
@media (max-width:768px) {
 .bottom-container{
    flex-direction: column;
 }
 .bottom-container .map-container{
    text-align: center;
 }
 .bottom-container .info{
    padding-top: 20px;
 }
 .contactUs-container{
    flex-direction: column;
    height: 180vh;
 }
 .contact-container{
    margin-top: 160px;
    width: 80%;
    height: 65vh;
    margin-right: 50px;
    margin-left: 50px;
    max-height: 65vh;
 }
 .contact-box{
    width: 100%;
 }
.left-box .left-contact-container .second{
    gap: 115px;
 }
}
@media (max-width:426px) {
    .left-box .left-contact-container .second{
      flex-direction: column;
     }
     .iframe{
        height: 320px;
        width: 355px;
     }
     .left-box{
        width: 100%;
     }
     .left-contact-container {
        height: 115vh;
        width: 80%;
        padding-top: 12px;
        padding-left: 25px;
     }
     .left-box .left-contact-container .second {
        gap: 25px;
        padding-top: 20px;
    }
    .contact-container{
        height: 66vh;
        max-height: 70vh;
        margin-right: 0px;
        margin-left: 25px;
        margin-top:0px;
    }
    .left-box{
        height: 95vh;
    }
}

@media (max-width:376px) {
    .iframe{
        height: 320px;
        width: 315px;
     }
     .left-box{
        height: 98vh;
     }
}