@import url('https://fonts.googleapis.com/css2?family=Arsenal+SC:ital,wght@0,400;0,700;1,400;1,700&display=swap');
.footer{
    margin-top: 20px;
    background-color: #232F3E;
    width: 100%;
    position: relative;
    color: white;
    min-height: 40vh;
    font-style: Static;
}
.footer-sections{
    display: flex;
    justify-content: space-between;
    padding-top: 40px;
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 20px;
}
.footer-head{
    font-size: 18px;
    color: grey;
    font-weight: 300;
    font-weight: 500;
}

.footer-social-media-img img{
    width: 25px;
    height: 25px;
}
.footer-social-media-img{
    color: white;
}
.footer-social-media-img li{
    padding-top: 10px;
    list-style: none;
    display: flex;
    gap: 10px;
    flex-direction: row;
    align-items: center;
}
.footer-social-media-img li p{
    font-size: 12px;
    font-weight: bold;
}
.footer-social-media-img li{
    cursor: pointer;
}
.footer-social-media-img li p:hover,.footer-about-item p:hover{
border-bottom: 1px solid white;
}
.footer-social-media-img a{
    text-decoration: none;
    color: white;
}
.footer-about-item{
    list-style: none;
}
.footer-about-item a{
    text-decoration: none;
}
.footer-about-item p{
    cursor: pointer;
    font-size: 15px;
    padding-top: 5px;
    font-weight: bold;
    color: white;
}
.copyright p{
    font-weight: bold;
    font-size: 20px;
    padding-top: 10px;
    padding-bottom: 15px;
    text-align: center;
}
@media (max-width:426px) {
    .footer-sections{
        justify-content: none;
        flex-direction: column;
        padding-top: 20px;
        padding-left: 20px;
    }
    .footer-sections div{
        padding-bottom: 10px;
    }
    .copyright p{
        font-size: 12px;
    }

}