.product-container{
    min-height: 100vh;
    display: flex;
    justify-content: space-between;
    margin-left: 0px;
    margin-right: 0px;
   margin-top: 0px;
   padding-top: 50px;
   margin-bottom: 0px; 
   background-color: white;
   padding: 20px;
}
 .top{
    padding-top: 20px;
 }
.product-image-container{
    width: 53%;
    display: flex;
    flex-direction: row;
}
.images-container img{
    width: 65px;
    height: 60px;
    padding: 1px;
    margin-left: 20px;
    margin-bottom: 30px;
    border-radius: 2px;
    cursor: pointer;
    border: 1px solid black;
}
.images-container{
    padding-top: 50px;
}
.images-container img:hover{
    border: 4px solid #002984;
    border-radius: 5px;
}
.main-image .img{
    border-radius: 2px;
    width: 650px;
    height: 650px;
    margin-left: 5px;
}
.main-image{
    width: 650px;
    height: 650px;
}
.product-info-container{
width: 46%;
margin-left: 10px;
}

.product-info-container .name{
    padding-top: 50px;
    font-size: 35px;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.908);
    font-family: Static;
}
.product-info-container p{
    padding-top: 5px;

}
.product-info-container .brand{
    font-size: 25px;
    padding-top: 10px;
    font-weight: 300;
    color: rgba(0, 0, 0, 0.87);
    font-family: Static;
    padding-bottom: 5px;
}
.product-info-container .price{
    font-size: 30px;
    color: black;
    font-weight: 500;
    font-family: Static;
    padding-top: 30px;
}
.product-info-container .fullfill{
    margin-top: 20px;
background-color: #002984;
padding: 2px;
width: 192px;
border-radius: 2px;
color: white;
font-weight: bold;
}

.product-info-container .deliverycharge p{
    padding-top: 10px;
font-size: 20px;
font-family: Static;
width: 70%;
}

.product-info-container .desc{
    margin-top: 10px;
    font-family: Static;
    padding: 10px;
    padding-right: 100px;
}
.product-info-container .desc-head{
    font-size: 25px;
    color: black;
    font-weight: bold;
    font-family: Static;
}
.product-info-container .des-item{
    font-size: 20px;

    color: rgba(0, 0, 0, 0.71);
}