.delivery-information{
    margin-top: 20px;
    padding-top: 20px;
    min-height: 80vh;
    padding-left: 10px;
    padding-right:10px ;
    width: 50%;
    display: flex;
    flex-direction: column;
    margin: auto;
    background-color: white;
    border-radius: 12px;
    margin-top: 20px;
    padding: 20px;
    box-shadow: 3px 3px 3px 3px  rgba(0, 0, 0, 0.479);
}
.delivery-information .delivery-text{
    font-size: 40px;
    font-weight: bold;
    font-style: Static;
    color: #002984;
}
.delivery-inputs{
    display: flex;
    flex-direction: column;
}
.delivery-information input{
    margin-bottom: 5px;
}
.contact-info input,.address-info input,.additional-info input{
    width: 95%;
    padding: 10px;
    border-radius: 5px;
    border:1px solid black;
    outline: none;
    font-size: 15px;
}
.contact-info .name,.address-info .name{
    display: flex;
    justify-content: space-between;
}
.contact-info .name input,.address-info .name input {
    padding: 10px;
    width: 90%;
    border-radius: 5px;
    border: 1px solid black;
    outline: none;
}
.contact-info .name .Name,.address-info .name .Name{
    display: flex;
    flex-direction: column;
    width: 48%;
}
.contact-info p, .address-info p,.additional-info p{
    padding-top: 2px;
    font-size: 25px;
    font-weight: 500;
}
.delivery-information span{
    padding-top: 2px;
    font-size: 20px;
}

.place-order-btn{
    text-align: center;
    padding-top: 20px;
    padding-bottom: 5px;
}
.place-order-btn button{
    cursor: pointer;
    padding: 10px;
    width: 100%;
    font-size: 30px;
    text-align: center;
    border-radius: 5px;
    background-color: #ff8f00;
    color: white;
    border: 1px solid orange;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.275);
}
.place-order-btn button:hover{
    background-color: #ff9100bf;
    padding: 11px;
    width: 101%;
}
@media (max-width:1024px) {
    .delivery-information{
        width: 85%;
    }
}
@media (max-width:768px) {
    .delivery-information{
        width: 92%;
    }
}
@media (max-width:426px) {
    .delivery-information{
        margin-top: 0px;
        width: 93%;
        box-shadow: 0px 0px 0px 0px white;
        border-radius: 0;
    }
}
@media (max-width:376px) {
    .delivery-information{
        width: 90%;
    }
    .delivery-information .delivery-text{
        font-size: 33px;
    }
}