

.products-list{
    margin-left: 10px;
    width: 98%;
    border-radius: 5px;
    background-color: white;
}
.pageNum{
    text-align: center;
    width: 40%;
    margin-left: 10px;
    border-radius: 15px;
    margin: auto;
    padding: 15px;
    align-items: center;
    border: 1px solid #002984;
    margin-top: 20px;
    background-color: white;
}
@media (max-width:768px) {
    .pageNum{
        width: 60%;

    }
}
@media (max-width:426px) {
    .caro{
        display: none;
    }
    .pageNum{
        width: 99%;
        border: none;
        background-color: #f0f0f0;
        border-radius: 5px;
        padding: 3px;
    }
}

