@import url('https://fonts.googleapis.com/css2?family=Arsenal+SC:ital,wght@0,400;0,700;1,400;1,700&display=swap');
.container{
    margin-top: 10px;
    padding-top: 5px;
    padding: 10px;
    font-family: Static;
}
.product-list{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
    margin: 10px;
    gap: 25px;
    row-gap: 40px;
}
.pagination-container{
    padding-top: 20px;
    display: flex;
    align-items: center;
    padding: 20px;
}
.pagination {
  font-size: 20px;
  margin-bottom: 0px;

}
.pagination ul{
    justify-content: space-between;
}
.product-list .Card{
    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.238);
    width: 275px;
    border-radius: 5px;

}

@media (max-width:768px) {
    .product-list{
        gap: 2px;
        row-gap: 10px;
        margin: 2px;
    }
}

