@import url('https://fonts.googleapis.com/css2?family=Arsenal+SC:ital,wght@0,400;0,700;1,400;1,700&display=swap');
.cart-container{
    font-family: Static;
    display: flex;
   flex-direction: row;
   margin-top: 20px;
   margin-left:125px;
   margin-right: 125px;
   padding: 5px;
   border-radius: 5px;
}
.cart-products{
    border-radius: 12px;
    width: 70%;
    background-color: white;
    padding: 10px;
    box-shadow: 2px 2px 2px 2px  rgba(0, 0, 0, 0.208);
}
.price-detail{
    box-shadow: 2px 2px 2px 2px  rgba(0, 0, 0, 0.208);
    padding: 20px;
    border-radius: 2px;
    margin-left: 10px;
    width: 30%;
    background-color: white;
  max-height: 300px;
  min-height: 300px;
}
.cart-product-container{
   display: flex;
   padding: 20px;
   flex-direction: row;
   padding-top: 20px;
   padding-bottom:2px ;
}


.product-name,.product-price,.product-qty,.product-total-amount,.product-brand{
    padding-bottom: 5px;
}
.cart-heading{
    font-size: 30px;
    font-weight: bolder;
    padding-top: 10px;
    padding-bottom: 15px;
   color: #002984;
}
.product-name{
    font-size: 25px;
    font-weight: bold;
}
.product-brand{
    font-size: 15px;
    color: grey;
}
.product-price{
    font-size: 15px;
    color: grey;
}
.product-qty{
    font-size: 15px;
    color: grey;
}
.product-total-amount{
    font-size: 20px;
    font-weight: bold;
}
.remove-item{
    justify-content: space-between;
    align-items: center;
    text-align: end;
}
.remove-item span{
    font-size: 20px;
}
.set-qty{
    display: flex;
    flex-direction: row;
    padding-bottom: 20px;
    align-items: center;
}
.set-qty p{
    margin-top: -5px;
    font-size: 20px;
    padding-left: 10px;
    padding-right: 10px;
}
.item-qty-main{
    align-items: center;
    display: flex;
    justify-content: space-between;
}
.remove-icon{
    gap: 7px;
    align-items: center;
    display: flex;
    flex-direction: row;
    cursor: pointer;
}
.remove-icon p{
    font-size: 20px;
}
.remove-icon p:hover{
    font-size: 22px;
}
.remove-icon:hover{
    color: blue;
}
.price-detail-heading{
    color: #002984;
    font-size: 25px;
    font-weight: bold;
}
.price-detail-container{
    padding-top: 10px;
    padding: 10px;
}
.space{
    font-size: 20px;
    font-weight: 500;
    display: flex;
    justify-content: space-between;
}
.space p{
    padding-top: 10px;
    padding-bottom: 10px;
}
.total-amount{
    padding-bottom: 15px;
    padding-top: 15px;
    display: flex;
    justify-content: space-between;
}
.total-amount p{
    font-size: 25px;
    font-weight: bold;
}
.button{
    padding-top: 10px;
    padding-bottom: 10px;
}

.empty-cart{
    display: flex;
    justify-content: center;
    align-items: center;
}
.cart-box{
    margin-top: 20px;
    border-radius: 2px;
    padding: 10px;
    background-color: white;
    box-shadow: 1px 0.5px 0.5px 0.5px  rgba(0, 0, 0, 0.096);
    min-width: 80%;
    max-width: 80%;
    min-height: 50vh;
    max-height: 80vh;
}
.cart-box img{
    width: 250px;
    height: 170px;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.empty-cart-img{
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: 50px;
   padding: 20px;
}
.cart-box p{
    font-size: 18px;
    padding-top: 20px;
}
.cart-box span{
    font-size: 12px;
    padding-top: 10px;
}
.button button{
text-align:center;
padding:8px;
font-size: 22px;
background-color:#e65100 ;
color: white;
width: 100%;
border: none;
box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.318);
border-radius: 5px;
}
@media (max-width:1024px) {
    .price-detail{
    padding: 10px;
    max-height: 275px;
    min-height: 275px;
    }
    .total-amount p{
        font-size: 20px;
    }
    .price-detail-container{
        padding: 0px;
    }
    .cart-container{
        margin-top: 10px;
        margin-left: 20px;
        margin-right: 26px;
    }
}

@media (max-width:768px) {
    .price-detail{
        width: 40%;
    }
}
@media (max-width:426px) {
    .cart-container{
        display: grid;
        margin-top: 0px;
        margin-left: 0px;
        margin-right: 0px;
    }
    .cart-products{
        width: 94%;
        border-radius: 0;
        box-shadow: 0px 0px 0px 0px white;
    }
    .price-detail{
        width: 94%;
        border-radius: 0px;
        margin-left: 0px;
        padding-top: 20px;
        box-shadow: 0px 0px 0px 0px white;
    }

}