.cart-btn{
    display: flex;
    flex-direction: row;
    gap: 50px;
    padding-top: 20px;
}
.addBTN, .buyNow{
    color: white;
    font-size: 32px;
    border: none;
    cursor: pointer;
    padding: 14px 60px;
}
.mobile-images-container img{
    cursor: pointer;
}
.addBTN{
    background-color: #ff8f00;
}
.addBTN:hover{
    background-color: #ff9100e1;
}
.buyNow{
    background-color: #ef6c00;
}
.buyNow:hover{
    background-color: #ef6c00e3;
}
.product-desc{
    background-color: white;
    padding: 20px;
    padding-top: 20px;
    font-family: Static;
}
.product-desc h3{
    padding-top: 0px;
    padding-left: 30px;
    font-size: 30px;
    color: black;
    font-weight: bold;
   padding-bottom: 10px;
}
.product-desc p{
    padding: 10px;
    padding-left: 60px;
    padding-right: 30px;
    font-size: 20px;
    color: rgba(0, 0, 0, 0.816);
    font-weight: 500;
    font-family: Static;
    padding-bottom: 10px;
}
.banners{
    background-color: white;
    padding-top: 15px;
    padding-bottom: 30px;
}
.banners h2{
font-size: 25px;
    padding-left: 20px;
    font-weight: bold;
    font-family: Static;
}
.banner-img{
    text-align: center;
    padding-top: 30px;
}

.banners img{
    text-align: center;
    margin-bottom: 0px;
    margin-top: -4px;
}
.images-container .active,.mobile-images-container .active{
    border: 4px solid #002984;
}

.similar-products-container{
    padding-top: 20px;
    background-color: white;
    border-radius: 5px;
    padding: 10px;
}
.similar-products-container .heading{
 font-size: 35px;
 color: black;
 font-weight: bold;
 font-family: Static;
 padding-top: 2px;
 padding-left: 5px;
}
.similar-products{
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 50px;
    display: flex;
    justify-self: space-between;
    gap: 40px;
    margin: 20px 0;
    overflow-x: scroll;
}
.similar-products::-webkit-scrollbar{
    display: none;
}
.similar-products img{
    width: 230px;
    height: 200px;
}
.similar-products .card{
    cursor: pointer;
    padding: 0px;
    border-radius: 5px;
    width: 250px;
    border: 1px solid black;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.504);
    height: 352px;
}

.similar-products .card .sInfo{
    padding: 10px;
    height: 130px;
    background-color: rgba(128, 128, 128, 0.303);
}
.btn-container{
    margin-top: 5px;
    padding-bottom: 5px;
    text-align: center;
}
.similar-products .card .sInfo .sBtn{
    font-size: 15px;
    color: white;
    padding: 7px 60px;
    background-color: #ff8f00;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.504);
}
.similar-products .card .sInfo .sBtn:hover{
    padding: 8px 62px;
    background-color: #ff9100a1;
}
.sName{
    color: #002984;
}
.sName:hover{
    color: #002a8491;
    cursor: pointer;
}
.mobile-images-container{
    display: none;
}
@media (max-width:1024px) {
    .main-image .img{
        width: 525px;
    }
    .main-image{
        width: 430px;
    }
    .addBTN, .buyNow{
        font-size: 20px;
    }
}
@media (max-width:768px) {
    .images-container{
        display: none;
    }
    .banners img{
        width: 720px;
    }
    .main-image .img{
        height: 400px;
        margin-left: -50px;
        width: 480px;
    }
    .addBTN, .buyNow{
        font-size: 15px;
    }
    .main-image{
        width: 385px;
    }
    .product-info-container .des-item {
        font-size: 15px;
        color: rgba(0, 0, 0, 0.71);
    }
    .product-info-container .deliverycharge p{
        width: 100%;
    }
    .product-info-container .desc{
         padding-right: 0px;
    }
    .mobile-images-container{
        padding-top: 20px;
    }
    .mobile-images-container img{
        border: 1px solid black;
        border-radius: 2px;
        width: 65px;
        height: 65px;
    }
    .mobile-images-container{
        display: flex;
        flex-direction: row;
        gap: 10px;
    }
}
@media (max-width:426px) {
    .banners img{
        width: 400px;
    }
    .product-container{
        display: flex;
        flex-direction: column;
    }
    .main-image{
        height: 530px;
    }
    .product-info-container{
        width: 100%;
    }
    .product-info-container .name{
        font-size: 30px;
        padding-top: 40px;
    }
    .product-info-container .brand{
        font-size: 20px;
        padding-top: 5px;
    }
    .product-info-container .price{
        padding-top: 5px;
    }
    .product-info-container .fullfill{
        margin-top: 5px;
    }
    .product-info-container .desc{
        padding: 0px;
    }
    .product-desc{
        padding: 0px;
        padding-top: 5px;
    }
    .main-image .img{
        width: 450px;
    }
    .product-desc h3{
        padding-left: 5px;
    }
    .product-desc p{
        padding: 0px;
        padding-left: 10px;
        padding-right: 5px;
        font-size: 15px;
    }
    .similar-products img{
        width: 185px;
    }
    .similar-products .card{
        width: 185px;
        height: 347px;
    }
    
.similar-products .card .sInfo .sBtn{
    padding: 6px 40px;
    font-size: 14px;
}

.similar-products .card .sInfo{
    padding: 7px;
}
.similar-products{
    padding-bottom: 0px;
    gap: 10px;
}
}
@media (max-width:376px) {
    .main-image .img{
        width: 405px;
    }
    .cart-btn{
        display: flex;
        flex-direction: row;
        gap: 60px;
    }
    .addBTN,.buyNow{
        padding: 11px 35px;
    }
    .mobile-images-container img{
        border: 1px solid black;
        border-radius: 2px;
        width: 55px;
        height: 55px;
    }
    .banner-img{
        padding-top: 10px;
    }
    .banners img{
        width: 348px;
    }
}
