@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200..700&display=swap');
.Nav-Bar{
    background-color: white;
    padding: 2px;
    align-items: center;
}
.shopName{
    display: flex;
    flex-direction: row;
    padding-left: 25px;
    font-family: Oswald;
    cursor: pointer;
}
.shopName p{
    font-size: 22px;
    font-weight: bolder;
}
.shopName:hover{
    color: #002a84c0;
}
.nav-item ul{
    display: flex;
    flex-direction: row;
    gap: 49px;
    font-size: 20px;
    padding-left:190px ;
}

.nav-item li{
    text-decoration: none;
    font-size: 20px;
    font-family: Oswald;
    list-style-type: none;
    color: #093170;
    font-weight: 450;
}

.nav-item li:hover{
    cursor: pointer;
    color: #093070c0;
}
.search{
    background-color: #e0e0e0;
    margin-left: 200px;
    width: 25%;
    border-radius: 5px;
}

.Nav-Bar{
    display: flex;
    flex-direction: row;
}
.Nav-Bar .sign{
    color: #37474f;
    border-radius: 5px;
    justify-content: center;
    margin-left: 40px;
        border:1px solid black;
     padding: 10px;
     font-size: 15px;
     font-weight: bold;
}
.Nav-Bar .sign:hover{
    cursor: pointer;
    background-color: rgba(156, 143, 143, 0.649);
}
.cart{
    padding-left: 40px;
}
.Nav-Bar .nav-item .active{
    border-bottom: 2px solid #093170;
}
.cartCount{
    border-radius: 20px;
    text-align: center;
    justify-content: center;
    width: 21px;
    background: black;
    font-size: 15px;
    justify-content: center;
    margin-left: -12px;
    margin-top: 8px;
    position: absolute;
    color: white;
    font-weight: bolder;
    height: 21px;
  }
.cart-icon{
    cursor: pointer;

}
.cart-icon:hover{
    color: rgba(0, 0, 0, 0.612);

}
.mobilenav{
    display: none;
}

.navbar-profile{
    position: relative;
    cursor: pointer;
}
.nav-profile-dropdown{
    position: absolute;
    display: none;
    right: 0;
    z-index: 1;
}
.navbar-profile{
    padding-left: 50px;
}
.navbar-profile:hover .nav-profile-dropdown{
    display: flex;
    flex-direction: column;
    gap: 10px;
    background-color: white;
    padding: 12px 25px;
    border-radius: 4px;
    border: 1px solid #002984;
    outline: 2px solid white;
}
li{
    list-style: none;
}
.nav-profile-dropdown li{
    cursor: pointer;
}
.nav-profile-dropdown li:hover{
    background-color: rgba(255, 255, 255, 0.653);
}
@media (max-width:1440px) {
    .nav-item ul{
        gap: 14px;
        display: flex;
    }
}
@media (max-width:1024px) {
    .search{margin-left: 50px;}
    .nav-item ul{
        padding-left: 30px;
        display: flex;
    }
    .shopName{
        padding-left: 10px;
    }
}
@media (max-width:768px) {
    .shopName{
        padding-left: 2px;
    }
    .nav-item ul{
        padding-left: 10px;
        display: flex;
        gap: 10px;
    }
    .search{
        margin-left: 10px;
        width: 20%;
    }
    .cart{
        padding-left: 10px;
    }
    .Nav-Bar .sign{
        margin-left: 5px;
    }
}
@media (max-width:426px) {
    .nav-item ul{
     display: none;
    }
    .sign{
        display: none;
    }
    .search{
        width: 30%;
    }
    .cart LocalMallIcon{
        display: flex;
        text-align: end;
    }
    .mobilenav{
        display: flex;
    }
    .shopName p{
        font-size: 17px;
    }
    .navbar-profile{
        padding-left: 0px;
    }
}