 .mobile-nav-item{
    padding: 10px;
 }
 .mobile-nav-item li:hover{
    background-color: rgba(128, 128, 128, 0.248);
    color: blue;
    width: 100%;
 }
.mobile-nav-heading{
    display: flex;
    justify-content: space-between;
    padding-bottom: 5px;
    gap: 50px;
    align-items: center;
}
.mobile-nav-heading p{
    font-size: 25px;
    font-weight: bold;
}
.mobile-nav-item li {
list-style: none;
padding-bottom: 10px;
font-size: 20px;
font-weight: 500;
}