.about-container{
    background-color: #f5f5f5;
    margin-bottom: -22px;
}
.about-container .first{
    padding-top: 125px;
    padding-left: 25px;
}
.about-container .first .heading{
    font-size: 50px;
    width: 30%;
    font-weight: bolder;
    font-family: "Roboto";
    color: #212121;
}
.about-container .first .sub-heading{
    font-size: 20px;
    padding-top: 5px;
    width: 35%;
    color: #212121;
}

.about-container .about-second{
    color: #212121;
    padding-top: 150px;
    padding-left: 25px;
}
.about-container .about-second{
    display: flex;
    justify-content: space-between;
}
.about-container .about-second .left .heading{
    font-size: 45px;
    color: #212121;
    font-weight: bolder;
}

.about-container .about-second .right img{
    max-width: 100%;
    width: 520px;
    height: 520px;
    max-height: 100%;
    border-radius: 5px;
    padding-top: 150px;
    margin-right: 150px;
}
.about-second .left .sub-heading{
    font-size: 20px;
    padding-top: 5px;
    width: 50%;
    color: #212121;
}
.left .trust .heading{
    padding-top: 40px;
    display: flex;
    flex-direction: direction;
    gap: 10px;
}
.left .trust .heading p{
    font-size: 20px;
    color: #212121;
}
.left .trust .sub-heading{
    color: #212121;
    font-size: 18px;
    padding-left: 35px;
    width: 40%;
}


.third-container{
    padding-top: 150px;
}

.third-container .main-heading{
    font-size: 35px;
    text-align: center;
    color: #212121;
    font-weight: bolder;
}
.third-container .sub-heading{
    font-size: 20px;
    text-align: center;
    padding-top: 5px;
    color: #212121;
}
.about-cards{
    display: flex;
    justify-content: space-between;
    gap: 30px;
    padding-top: 50px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 100px;
}
.about-cards div{
    box-shadow: 3px 3px 3px 3px black;
    background-color:black;
    width: 350px;
    height: 150px;
    justify-content: center;
    align-items: center;
    padding: 30px;
    border-radius: 15px;
}
.about-cards div p{
    color: white;
    font-size: 20px;
}
.about-cards div .heading{
    text-align: center;
    font-size: 24px;
    font-weight: bold;
}

