.myorders{
    background-color: white;
    margin-top: 20px;
    margin-left: 150px;
    margin-right: 150px;
    min-height: 120vh;
    padding: 20px;
    border-radius: 10px;
}
.order-heading{
    padding-bottom: 10px;
}
.myorders img{
    width: 150px;
    height: 150px;
}
.myorders .item-order{
        display: flex;
        flex-direction: row;
        padding: 10px;
}
.order{
    padding: 12px;
    display: flex;
    flex-direction: column;
}
.order .status{
    font-size: 22px;
    color: #212121;
    font-weight: bold;
}
.myorders .item-order p{
    font-size: 22px;
    color: #212121;
    font-weight: bold;
}
@media (max-width:426px) {
    .myorders{
        margin-top: 10px;
        margin-left: 10px;
        margin-right: 10px;
    }
}