.card{
border-radius: 10px;
width: 275px;
box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.238);
height: 500px;
}
.card .item-info{
  background-color: rgba(128, 128, 128, 0.252);
  padding: 5px;
  height:185px ;
  border-radius: 10px;
}
.card img{
    width: 275px;
    height: 300px;
    cursor: pointer;
}
.item-info p{
  font-family: 'Gill Sans';
}
.item-name{
  color: #002984;
  font-size: 20px;
  font-weight: 400;
  cursor: pointer;
}
.item-name:hover{
  color: blue;
}
.item-price{
  padding-top: 2px;
  color: black;
  font-size: 25px;
  font-weight: bolder;
}
.item-brand{
  font-weight: bold;
  padding-top:2px ;
  color: black;
  font-size: 20px;
  font-weight: 500;
  padding-bottom: 8px;
}
.btn{
  margin-bottom: 2px;

}
.btn:hover{
background-color: #002a84c3;
}
.cart-value{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cart-value p{
  font-size: 32px;
  color: black;
  padding: 5px;
  font-weight: bold;
}

@media (max-width:768px) {
  .card{
    height: 480px;
    width: 205px;
  }
  .card img{
    width: 205px;
    height: 250px;
  }
  .card .item-info{
    height: 215px;
  }
}
@media (max-width:426px) {
  .card{
    margin: auto;
    width: 98%;
    height: 395px;
  }
  .card img{
    width: 98%;
    height: 192px;
  }
  .card .item-info{
    height: 191px;
  }
}
